// InputCaller.js

import { structureBoxes } from './StructureBoxes.js';
import { drawElbowLines } from './DrawLines.js'; // Import new function
import { initializeDragging } from "./Draggable.js";

const ORG_TABLE_NAME = "OrgData";

export async function createTable() {
    await Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();
        const activeCell = context.workbook.getActiveCell();
        activeCell.load("rowIndex, columnIndex");
        await context.sync();

        const startRow = activeCell.rowIndex;
        const startCol = activeCell.columnIndex;

        // Remove existing table if present
        sheet.tables.load("name");
        await context.sync();
        sheet.tables.items
            .filter(t => t.name === ORG_TABLE_NAME)
            .forEach(t => t.delete());

        const orgData = [
            // --- TIER 1: Executive ---
            { id: 1, parent: null, name: "CEO Office", colour: "#2c3e50", lines: ["Strategic Direction", "Founder", "#Top Level"] },

            // --- TIER 2: Division Management ---
            { id: 2, parent: 1, name: "VP Technology", colour: "#34495e", lines: ["IT & Engineering", "#Management"] },
            { id: 3, parent: 1, name: "VP Operations", colour: "#34495e", lines: ["Logistics & HR", "#Management"] },

            // --- TIER 3: Department Management ---
            { id: 4, parent: 2, name: "Head of Software", colour: "#2980b9", lines: ["Development Dept", "#Product", "#Innovation"] },
            { id: 5, parent: 2, name: "Head of Infrastructure", colour: "#2980b9", lines: ["Cloud & Networks", "#Product"] },
            { id: 6, parent: 3, name: "Head of Logistics", colour: "#e67e22", lines: ["Supply Chain", "#Operations"] },

            // --- TIER 4: Team Leads ---
            { id: 7, parent: 4, name: "Backend Lead", colour: "#7f8c8d", lines: ["API Team", "#Product", "#Technical"] },
            { id: 8, parent: 4, name: "Frontend Lead", colour: "#7f8c8d", lines: ["UI Team", "#Product", "#Technical"] },
            { id: 9, parent: 6, name: "Warehouse Manager", colour: "#7f8c8d", lines: ["Distribution", "#Operations", "#Logistics"] },

            // --- TIER 5: Individual Contributors ---
            { id: 10, parent: 7, name: "Senior Developer", colour: "#95a5a6", lines: ["Backend Service", "#Technical", "#Remote"] },
            { id: 11, parent: 7, name: "Junior Developer", colour: "#95a5a6", lines: ["Backend Service", "#Technical", "#Remote"] },
            { id: 12, parent: 8, name: "UI Designer", colour: "#95a5a6", lines: ["Frontend Team", "#Product", "#Remote"] },
            { id: 13, parent: 9, name: "Logistics Coordinator", colour: "#95a5a6", lines: ["Shipping", "#Operations", "#Office"] },
            { id: 14, parent: 9, name: "Driver", colour: "#95a5a6", lines: ["Delivery", "#Operations", "#Field"] }
        ];

        // Find the max number of lines to create enough columns
        const maxLines = Math.max(...orgData.map(o => o.lines.length));

        // Header row
        const headers = ["id", "parent", "name", "colour", "image", "custom header", "custom header 2", "!hidden header"];

        // Rows
        const rows = orgData.map(o => {
            const lineCols = o.lines.slice(0, maxLines);
            while (lineCols.length < maxLines) lineCols.push(""); // pad if needed
            return [o.id, o.parent ?? "", o.name, o.colour, o.image ?? "", ...lineCols];
        });

        // Use the active cell as starting point
        const range = sheet.getRangeByIndexes(startRow, startCol, rows.length + 1, headers.length);
        range.values = [headers, ...rows];

        const table = sheet.tables.add(range, true);
        table.name = ORG_TABLE_NAME;

        sheet.activate();
        await context.sync();
    });
}
window.createTable = createTable;


export async function refresh() {
    const root = document.getElementById("org-container");
    const chart = document.getElementById("ChartType").value;
    const line = document.getElementById("LineType").value;
    root.innerHTML = "";

    const orgData = await loadOrgDataFromExcel();

    if (!validateHierarchy(orgData)) {
        console.error("Circular dependency detected in hierarchy!");
        alert("Error: The organization chart contains a circular dependency (e.g., a person reporting to themselves or in a loop).");
        return;
    }

    root.appendChild(structureBoxes(orgData, chart, line));
    drawElbowLines();
    initializeDragging();
}
window.refresh = refresh;

async function loadOrgDataFromExcel() {
    return Excel.run(async (context) => {
        try {
            // Attempt to get the table
            const table = context.workbook.tables.getItem(ORG_TABLE_NAME);

            // Load header row and data body
            const headerRange = table.getHeaderRowRange();
            const bodyRange = table.getDataBodyRange();

            headerRange.load("values");
            bodyRange.load("values");
            await context.sync();

            const headers = headerRange.values[0];
            const reserved = ["id", "parent", "name", "colour", "image"];

            return bodyRange.values.map(row => {
                const lines = row
                    .map((cell, i) => ({ value: cell, header: headers[i] }))
                    .filter(col => {
                        if (!col.value) return false;
                        if (col.header.startsWith('!')) return false;
                        return !reserved.includes(col.header);
                    })
                    .map(col => col.value);

                return {
                    id: row[headers.indexOf("id")],
                    parent: row[headers.indexOf("parent")] || null,
                    name: row[headers.indexOf("name")],
                    colour: row[headers.indexOf("colour")],
                    image: row[headers.indexOf("image")] || null,
                    lines
                };
            });
        } catch (error) {
            // --- ALERT USER HERE ---
            alert(`The required table "${ORG_TABLE_NAME}" was not found. Please click "Create example table" first.`);
            return []; // Return empty data so the rest of the refresh function handles it gracefully
        }
    });
}

function validateHierarchy(data) {
    const nodes = new Map();
    // 1. Map nodes for quick lookup
    data.forEach(item => nodes.set(item.id.toString(), item));

    const visited = new Set();
    const stack = new Set();

    // 2. DFS function to detect cycles
    function hasCycle(id) {
        visited.add(id);
        stack.add(id);

        const node = nodes.get(id);
        if (node && node.parent) {
            const parentId = node.parent.toString();
            if (!visited.has(parentId)) {
                if (hasCycle(parentId)) return true;
            } else if (stack.has(parentId)) {
                return true; // Cycle detected
            }
        }

        stack.delete(id);
        return false;
    }

    // 3. Check every node
    for (const node of data) {
        if (!visited.has(node.id.toString())) {
            if (hasCycle(node.id.toString())) return false;
        }
    }

    return true;
}