// Exporter.js
import html2canvas from "html2canvas";

// --- HELPER: Fetch and Convert Image to Base64 ---
async function imageToDataURL(url) {
    try {
        const response = await fetch(url);
        const blob = await response.blob();
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onloadend = () => resolve(reader.result);
            reader.onerror = reject;
            reader.readAsDataURL(blob);
        });
    } catch (e) {
        console.error("Could not fetch image for CORS fix:", url);
        return null;
    }
}

// --- NEW HELPER: Small delay to allow DOM to update ---
const wait = (ms) => new Promise(resolve => setTimeout(resolve, ms));

export async function exportToPNG() {
    // --- 1. IMAGE PROCESSING (CORS Fix) ---
    const boxes = document.querySelectorAll('.org-box');
    for (const box of boxes) {
        const img = box.querySelector('img');
        if (img && img.src.startsWith('http')) {
            const dataUrl = await imageToDataURL(img.src);
            if (dataUrl) {
                img.src = dataUrl; // Replace external URL with Base64 data
            }
        }
    }

    // --- ADD THIS DELAY ---
    // Give the browser 500ms to repaint the images after setting src
    await wait(500);

    // --- 2. EXPORT LOGIC ---
    const organagram = document.getElementById('organagram');
    const container = document.getElementById("org-container");
    const originalStyle = organagram.getAttribute('style');

    // Reduce maximum width to prevent canvas corruption
    const MAX_WIDTH = 20000;
    const currentWidth = container.scrollWidth;

    // Scale only if content is wider than MAX_WIDTH
    const scaleFactor = currentWidth > MAX_WIDTH ? MAX_WIDTH / currentWidth : 1;

    // Apply temporary scaling for high-resolution capture
    organagram.style.transformOrigin = "top left";
    organagram.style.transform = `scale(${scaleFactor})`;
    organagram.style.width = `${currentWidth}px`;
    organagram.style.height = `${organagram.scrollHeight}px`;

    // --- 3. RENDER ---
    const canvas = await html2canvas(organagram, {
        backgroundColor: "#fff",
        logging: false,
        useCORS: true,
        // Match width/height to scaled dimensions perfectly
        width: currentWidth * scaleFactor,
        height: container.scrollHeight * scaleFactor,
        scale: window.devicePixelRatio, // High DPI support
        x: 0,
        y: 0
    });

    // --- 4. RESTORE ORIGINAL STYLE ---
    organagram.setAttribute('style', originalStyle || "");

    // --- 5. DOWNLOAD ---
    const pngUrl = canvas.toDataURL("image/png");
    const link = document.createElement('a');
    link.download = 'organogram.png';
    link.href = pngUrl;
    link.click();
}