// DrawLines.js
export function drawDirectLines() {
    // 1. Check if an old SVG exists
    const oldSvg = document.getElementById("org-lines-svg");
    let nextSibling = null;
    let parentNode = document.body;

    if (oldSvg) {
        nextSibling = oldSvg.nextSibling; // Remember the position
        parentNode = oldSvg.parentNode;   // Remember the parent
        oldSvg.remove();
    }

    // 2. Create the new SVG container
    const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    svg.id = "org-lines-svg";

    // Standard positioning styles
    svg.style.position = "absolute";
    svg.style.top = "8px";
    svg.style.left = "0";
    svg.style.pointerEvents = "none";
    svg.style.zIndex = "1";

    const fullWidth = Math.max(document.body.scrollWidth, document.documentElement.scrollWidth);
    const fullHeight = Math.max(document.body.scrollHeight, document.documentElement.scrollHeight);

    svg.setAttribute("width", fullWidth);
    svg.setAttribute("height", fullHeight);
    svg.setAttribute("viewBox", `0 0 ${fullWidth} ${fullHeight}`);

    // 3. Insert in the same position
    if (nextSibling) {
        parentNode.insertBefore(svg, nextSibling);
    } else {
        parentNode.appendChild(svg);
    }

    // 4. Draw the lines (using the Zoom-safe bodyRect logic)
    const boxes = document.querySelectorAll('.org-box');
    const bodyRect = document.body.getBoundingClientRect();

    boxes.forEach(childEl => {
        const parentId = childEl.getAttribute('value');
        if (parentId) {
            const parentEl = document.getElementById(parentId);
            if (parentEl) {
                const cRect = childEl.getBoundingClientRect();
                const pRect = parentEl.getBoundingClientRect();

                // Coordinates relative to the body origin
                const x1 = (pRect.left + pRect.width / 2) - bodyRect.left;
                const y1 = ((pRect.top + pRect.bottom) * 0.5) - bodyRect.top;

                const x2 = (cRect.left + cRect.width / 2) - bodyRect.left;
                const y2 = ((cRect.top + cRect.bottom) * 0.5) - bodyRect.top;

                const line = document.createElementNS("http://www.w3.org/2000/svg", "line");
                line.setAttribute("x1", x1);
                line.setAttribute("y1", y1);
                line.setAttribute("x2", x2);
                line.setAttribute("y2", y2);
                line.setAttribute("stroke", "#ccc");
                line.setAttribute("stroke-width", "2");

                svg.appendChild(line);
            }
        }
    });
}

export function drawElbowLines() {
    // 1. Check if an old SVG exists
    const oldSvg = document.getElementById("org-lines-svg");
    let nextSibling = null;
    let parentNode = document.body;

    if (oldSvg) {
        nextSibling = oldSvg.nextSibling;
        parentNode = oldSvg.parentNode;
        oldSvg.remove();
    }

    // 2. Create the new SVG container
    const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    svg.id = "org-lines-svg";
    svg.style.position = "absolute";
    svg.style.top = "0";
    svg.style.left = "0";
    svg.style.pointerEvents = "none";
    svg.style.zIndex = "1";

    const fullWidth = Math.max(document.body.scrollWidth, document.documentElement.scrollWidth);
    const fullHeight = Math.max(document.body.scrollHeight, document.documentElement.scrollHeight);
    svg.setAttribute("width", fullWidth);
    svg.setAttribute("height", fullHeight);

    if (nextSibling) {
        parentNode.insertBefore(svg, nextSibling);
    } else {
        parentNode.appendChild(svg);
    }

    // 3. Draw the Elbow Lines
    const boxes = document.querySelectorAll('.org-box');
    const bodyRect = document.body.getBoundingClientRect();

    boxes.forEach(childEl => {
        const parentId = childEl.getAttribute('value');
        if (!parentId) return;

        const parentEl = document.getElementById(parentId);
        if (!parentEl) return;

        const inputType = childEl.getAttribute('lineInput');
        const outputType = parentEl.getAttribute('lineOutput');

        const cRect = childEl.getBoundingClientRect();
        const pRect = parentEl.getBoundingClientRect();

        const startX = (pRect.left + pRect.width / 2) - bodyRect.left + 8;
        const startY = ((pRect.top + pRect.bottom) * 0.5) + 6 - bodyRect.top;

        const endX = (cRect.left + cRect.width / 2) - bodyRect.left + 8;
        const endY = ((cRect.top + cRect.bottom) * 0.5) + 6 - bodyRect.top;


        let pathData = "";

        // --- SCENARIO 1: Parent Bottom -> Child Top ---
        if (outputType === "bottom" && inputType === "top") {
            // 50% down, 100% across, then 50% down
            const midY = startY + (endY - startY) / 2;
            pathData = `M ${startX} ${startY} V ${midY} H ${endX} V ${endY}`;
        }
        // --- SCENARIO 2: Parent Bottom -> Child Side ---
        else if (outputType === "bottom" && inputType === "side") {
            // Down 100%, across 100%
            pathData = `M ${startX} ${startY} V ${endY} H ${endX}`;
        }
        // --- SCENARIO 3: Parent Side -> Child Side ---
        else if (outputType === "side" && inputType === "side") {
            // Across 50%, down 100%, across 50%
            const midX = startX + (endX - startX) / 2;
            pathData = `M ${startX} ${startY} H ${midX} V ${endY} H ${endX}`;
        }
        // --- SCENARIO 4: Parent Side -> Child Top ---
        else if (outputType === "side" && inputType === "top") {
            // Across 100%, down 100%
            pathData = `M ${startX} ${startY} H ${endX} V ${endY}`;
        }
        // --- SCENARIO 5: Parent Side -> Child ---
        else if (outputType === "side" && inputType === "direct") {
            pathData = `M ${startX} ${startY} L ${endX} ${endY}`;
        }
        // --- SCENARIO 6: Parent Bottom -> Child ---
        else if (outputType === "bottom" && inputType === "direct") {
            pathData = `M ${startX} ${startY} L ${endX} ${endY}`;
        }

        // Draw the path if data was generated
        if (pathData) {
            const path = document.createElementNS("http://www.w3.org/2000/svg", "path");
            path.setAttribute("d", pathData);
            path.setAttribute("stroke", "#ccc");
            path.setAttribute("stroke-width", "2");
            path.setAttribute("fill", "none");
            svg.appendChild(path);
        }
        // ... (rest of the loop) ...
    });
}

